<template>
    <div>
        <br />
        <div>
            <label>From word: </label>
            <input v-model="from_word_input" />
            <br />
            <label>To word: </label>
            <input v-model="to_word_input" />
        </div>
        <br />
        <button @click="generate" :disabled="running">Generate</button>
        <div v-if="progress > 0">{{ progress }}%</div>
        <div v-if="discovered.length > 0">Found word ladders with length: {{ discovered }}</div>
        <button @click="copy" v-if="test_code.length > 0">Copy</button>
        <pre v-if="test_code.length > 0">{{ test_code }}</pre>
    </div>
</template>

<script>
import { Generator } from "../scripts/test_generator";
import english from "raw-loader!../scripts/english.txt";

export default {
    methods: {
        copy() {
            navigator.clipboard.writeText(this.test_code);
        },
        process_test_items() {
            let sorted_length_keys = [];
            for (const key of this.test_items.keys()) {
                sorted_length_keys.push(key);
            }
            sorted_length_keys.sort((a, b) => a - b);
            this.test_code = `#include <comp6771/word_ladder.hpp>
#include <catch2/catch.hpp>
#include <string>
#include <vector>

auto ladder_eq(std::vector<std::vector<std::string>> const& A, std::vector<std::vector<std::string>> const& B) -> bool {
if (A.size() != B.size()) return false;
for (unsigned long k = 0; k < A.size(); ++k) {
if (A[k].size() != B[k].size()) return false;
for (unsigned long z = 0; z < A[k].size(); ++z) if (A[k][z] != B[k][z]) return false;
}
return true;}

TEST_CASE("Test generate - Word Ladders Exact Match - (Word of length ${this.word_length})") {
\tauto const english_lexicon = word_ladder::read_lexicon("./english.txt");
`;
            let i = 0;
            for (const key of sorted_length_keys) {
                const test = this.test_items.get(key);
                const [from_word, to_word, word_ladders, total_ladders] = [test.from_word, test.to_word, test.word_ladders, test.total_ladders];
                let auto_needed = "";
                if (i == 0) auto_needed = "auto ";
                ++i;
                this.test_code += `
\t${auto_needed}ladders = word_ladder::generate("${from_word}", "${to_word}", english_lexicon);
\tCHECK(std::size(ladders) == ${total_ladders});
\tCHECK(std::is_sorted(ladders.begin(), ladders.end()));
\tCHECK(ladder_eq(ladders, ${this.generator.parse_word_ladder(word_ladders)}));
`;
            }
            this.test_code += `}`;
        },
        stop() {
            this.running = false;
            this.process_test_items();
        },
        async generate() {
            if (this.running) return;
            const lexicon_map = this.lexicon_map;
            this.test_items = new Map();
            this.test_code = "";
            this.discovered = new Array();
            const word_1 = this.from_word_input;
            const word_2 = this.to_word_input;
            const word_ladders = this.generator.generate(word_1, word_2, lexicon_map.get(word_1.length));
            const word_ladders_length = word_ladders.length;
            const test_object = {
                from_word: word_1,
                to_word: word_2,
                word_ladders: word_ladders,
                total_ladders: word_ladders_length,
            };
            this.test_items.set(word_ladders_length, test_object);
            if (!this.discovered.includes(word_ladders_length)) {
                this.discovered.push(word_ladders_length);
                this.discovered.sort((a, b) => a - b);
            }
            this.process_test_items();
        },
    },
    created() {
        const generator = new Generator();
        const lexicon_list = english.trim().split("\n");
        const lexicon_set = new Set(lexicon_list);
        const lexicon_map = generator.get_lexicon_map(lexicon_set);
        const lexicon_maplist = generator.get_lexicon_maplist(lexicon_set);
        let lexicon_lengths = [];
        for (const len of lexicon_map.keys()) lexicon_lengths.push(parseInt(len));
        lexicon_lengths.sort((a, b) => a - b);
        this.generator = generator;
        this.word_lengths = lexicon_lengths;
        this.lexicon_list = lexicon_list;
        this.lexicon_set = lexicon_set;
        this.lexicon_map = lexicon_map;
        this.lexicon_lengths = lexicon_lengths;
        this.lexicon_maplist = lexicon_maplist;
    },
    data() {
        return {
            test_code: "",
            word_length: 2,
            word_lengths: [],
            tries: 1000,
            progress: 0,
            running: false,
            generator: null,
            lexicon_list: null,
            lexicon_set: null,
            lexicon_map: null,
            lexicon_maplist: null,
            lexicon_lengths: null,
            test_items: new Map(),
            discovered: new Array(),
            from_word_input: "deboned",
            to_word_input: "inditer",
        };
    },
};
</script>

