class t{parse_word_ladder(r){let n="{";for(let e=0;e<r.length;++e){const o=r[e];var a=o.map(t=>`"${t}"`).join(", ");let t=(0==e?"":"\t\t\t\t\t\t\t\t")+`{${a}}`;e<r.length-1&&(t+=",\n"),n+=t}return n+="}"}get_random_word_from_lexicon(t){return t[Math.floor(Math.random()*t.length)]}get_lexicon_maplist(t){let e=new Map;for(const n of t){var r=n.length;e.has(r)?e.get(r).push(n):e.set(r,new Array(n))}return e}get_lexicon_map(t){let e=new Map;for(const n of t){var r=n.length;e.has(r)?e.get(r).add(n):e.set(r,new Set([n]))}return e}neighbours(r,n){let a=[];for(let e=0;e<r.length;++e)for(let t="a".charCodeAt(0);t<="z".charCodeAt(0);t++){var o=String.fromCharCode(t);o!=r[e]&&(o=r.substring(0,e)+o+r.substring(e+1,r.length),n.has(o)&&a.push(o))}return a}build_ladder(t,e,r,n=[]){if(n.unshift(t),e.has(t))for(const a of e.get(t))this.build_ladder(a,e,r,n);else 2<=n.length&&r.push([...n]);n.shift()}generate(t,s,l){let e=[];if(t!==s&&l.has(t)&&l.has(s)){let[r,n,a,o]=[new Array(t),new Set([t]),new Map,!1];for(;0<r.length&&!o;){var h=r.length;let e=new Set;for(let t=0;t<h;++t){var d=r.pop();d===s&&(o=!0);for(const g of this.neighbours(d,l))n.has(g)||(e.add(g),a.has(g)?a.get(g).push(d):a.set(g,[d]))}for(const f of e)n.add(f),r.push(f)}o&&this.build_ladder(s,a,e)}return e.sort()}}export{t as Generator};