<template>
    <div>
        <Button @click="change_mode">{{ mode == 1 ? "Next" : "Previous" }}</Button>
        <br />
        <br />
        <div v-show="mode == 1">
            <TestGenerator />
        </div>
        <div v-show="mode != 1">
            <TestGenerator_2 />
        </div>
    </div>
</template>

<script>
import TestGenerator from "./components/TestGenerator.vue";
import TestGenerator_2 from "./components/TestGenerator_2.vue";

export default {
    name: "App",
    methods: {
        change_mode() {
            this.mode = (this.mode + 1) % 2;
        },
    },
    data() {
        return {
            mode: 1,
        };
    },
    components: {
        TestGenerator,
        TestGenerator_2,
    },
};
</script>

